.table_even,
.table_odd,
.table_even_odd,
.table_odd_even {
  width: 100%;
  text-align: left;
}
.table td,
.table th,
.table_even td,
.table_even th,
.table_odd td,
.table_odd th,
.table_even_odd td,
.table_even_odd th,
.table_odd_even td,
.table_odd_even th {
  padding: 10px 10px;
  vertical-align: middle;
}
.table thead th,
.table_even thead th,
.table_odd thead th,
.table_even_odd thead th,
.table_odd_even thead th {
  font-size: 1.1em;
  font-weight: 600;
}
.table_even tbody tr:nth-child(2n),
.table_even_even tbody tr:nth-child(2n),
.table_odd_even tbody tr:nth-child(2n),
.table_even_odd_even tbody tr:nth-child(2n),
.table_odd_even_even tbody tr:nth-child(2n) {
  background: #f2f2f2;
}
.table_odd tbody tr:nth-child(2n-1),
.table_even_odd tbody tr:nth-child(2n-1),
.table_odd_odd tbody tr:nth-child(2n-1),
.table_even_odd_odd tbody tr:nth-child(2n-1),
.table_odd_even_odd tbody tr:nth-child(2n-1) {
  background: #f2f2f2;
}
.thead {
  border-color: #ebebeb;
  border-style: solid;
  border-bottom-width: 1px;
}
h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
  color: #000;
  line-height: 1;
}
h1,
.h1 {
  margin-bottom: 20px;
  font-size: 36px;
}
h2,
.h2 {
  margin-bottom: 15px;
  font-size: 30px;
}
h3,
.h3 {
  margin-bottom: 10px;
  font-size: 24px;
}
h4,
.h4 {
  margin-bottom: 7px;
  font-size: 18px;
}
h5,
.h5 {
  margin-bottom: 5px;
  font-size: 14px;
}
h6,
.h6 {
  margin-bottom: 5px;
  font-size: 12px;
}
.h-none {
  color: #000;
  font: 400 14px / 1.2 "Helvetica Neue", Helvetica, Arial, sans-serif;
  letter-spacing: 0;
  word-spacing: 0;
  text-transform: none;
}
/* snippets */
.clfx:before,
.clfx:after,
.tab-dl:before,
.tab-dl:after {
  content: "";
  display: table;
}
.clfx:after,
.tab-dl:after {
  clear: both;
}
/**
 * extend .clfx from
 * core -> snippets -> float.less
 */
.tab-dl {
  width: 100%;
}
.tab-dl dt {
  float: left;
}
.tab-dl dd {
  display: none;
  float: right;
  width: 100%;
  margin: 50px 0 0 -100%;
  position: relative;
  z-index: 2;
}
.tab-dl dd.active {
  display: block;
}
.container {
  width: 960px;
  position: relative;
  margin: 0 auto;
}
